.production-card {
    color: #fff;
    width: 300px;
    height: 250px;
    margin: 15px;
    border: 1px solid white;
    display: flex;
    position: relative;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    z-index: 15;
    overflow: hidden;

    .production-image {
        width: 100%;
        height: 100%;
        background-position: center;
        background-size: cover;
    }

    .hover-background {
        position: absolute;
        width: 100%;
        height: 100%;
    }

    .card-title {
        position: absolute;
        display: flex;
        justify-content: center;
        align-items: center;
        font-weight: bold;
        font-size: 25px;
        margin: 0;
    }

    .card-content {
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        color: #fff;
        text-align: center;
        z-index: 20;
        background-color: rgba(0, 0, 0, 1);

        .production-link {
            width: 100%;
            height: 100%;
            cursor: pointer;
            text-decoration: none;
            outline: none;
            position: relative;
            display: flex;
            justify-content: center;
            align-items: center;
            z-index: 15;
            
            .card-text {
                align-self: center;
                font-weight: lighter;
                font-size: 17px;
                letter-spacing: 0.75px;
                margin: 0 13px;
            }
        }

    }
}

.show {
    opacity: 1;
}