.video-settings {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px 80px;

    .settings {
        width: 100%;
        height: 61px;
        display: flex;
    
        & .input {
            background-color: rgba(0, 0, 0, .2);
            border-top-left-radius: 25px;
            border-bottom-left-radius: 25px;
            padding: 1rem;
            width: 100%;
            color: #fff;
            height: 10px;
            margin-top: 15px;
            outline: none;
        }
    
        & .btn-submit {
            background-color: rgba(0, 0, 0, .2);
            border-top-right-radius: 25px;
            border-bottom-right-radius: 25px;
            border-left: none;
            width: 40px;
            color: #fff;
            margin-top: 15px;
            outline: none;
            text-align: center;
        }
    }

    .container {
        width: 100%;
        display: flex;
    }
}